"use client"

import { useState, useEffect } from "react"
import { useParams, useRouter } from "next/navigation"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { ArrowLeft } from "lucide-react"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"

export default function KardexPage() {
  const params = useParams()
  const router = useRouter()
  const [patient, setPatient] = useState(null)
  const [consultations, setConsultations] = useState([])

  useEffect(() => {
    fetchPatientData()
    fetchConsultations()
  }, [params.id])

  const fetchPatientData = async () => {
    const response = await fetch("/api/patients")
    const data = await response.json()
    const foundPatient = data.find((p) => p.id === params.id)
    setPatient(foundPatient)
  }

  const fetchConsultations = async () => {
    const response = await fetch("/api/consultations")
    const data = await response.json()
    const patientConsultations = data.filter((c) => c.patientId === params.id)
    setConsultations(patientConsultations)
  }

  if (!patient) {
    return (
      <div className="p-6">
        <p>Cargando...</p>
      </div>
    )
  }

  return (
    <div className="p-6">
      <Button variant="ghost" onClick={() => router.back()} className="mb-4">
        <ArrowLeft className="w-4 h-4 mr-2" />
        Volver
      </Button>

      <h1 className="text-3xl font-bold mb-6">Kardex del Paciente</h1>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-6">
        <Card className="p-6">
          <h2 className="text-xl font-bold mb-4">Información del Paciente</h2>
          <div className="space-y-3">
            <div className="grid grid-cols-2 gap-2">
              <span className="font-semibold">ID:</span>
              <span>{patient.id}</span>
            </div>
            <div className="grid grid-cols-2 gap-2">
              <span className="font-semibold">Nombre:</span>
              <span>{patient.name}</span>
            </div>
            <div className="grid grid-cols-2 gap-2">
              <span className="font-semibold">Tipo:</span>
              <span>{patient.animalType}</span>
            </div>
            <div className="grid grid-cols-2 gap-2">
              <span className="font-semibold">Raza:</span>
              <span>{patient.breed}</span>
            </div>
            <div className="grid grid-cols-2 gap-2">
              <span className="font-semibold">Edad:</span>
              <span>{patient.age} años</span>
            </div>
            <div className="grid grid-cols-2 gap-2">
              <span className="font-semibold">Peso:</span>
              <span>{patient.weight} kg</span>
            </div>
            <div className="grid grid-cols-2 gap-2">
              <span className="font-semibold">Sexo:</span>
              <span>{patient.sex}</span>
            </div>
          </div>
        </Card>

        <Card className="p-6">
          <h2 className="text-xl font-bold mb-4">Información del Dueño</h2>
          <div className="space-y-3">
            <div className="grid grid-cols-2 gap-2">
              <span className="font-semibold">Nombre:</span>
              <span>{patient.ownerName}</span>
            </div>
            <div className="grid grid-cols-2 gap-2">
              <span className="font-semibold">Teléfono:</span>
              <span>{patient.ownerPhone}</span>
            </div>
            <div className="grid grid-cols-2 gap-2">
              <span className="font-semibold">Última Visita:</span>
              <span>{new Date(patient.lastVisit).toLocaleDateString("es-MX")}</span>
            </div>
          </div>

          <div className="mt-4">
            <h3 className="font-semibold mb-2">Historial Médico:</h3>
            <p className="text-sm text-muted-foreground">{patient.medicalHistory}</p>
          </div>

          <div className="mt-4">
            <h3 className="font-semibold mb-2">Enfermedades:</h3>
            <p className="text-sm text-muted-foreground">{patient.diseases}</p>
          </div>
        </Card>
      </div>

      <Card className="p-6">
        <h2 className="text-xl font-bold mb-4">Historial de Consultas</h2>
        <div className="overflow-x-auto">
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Fecha</TableHead>
                <TableHead>Motivo</TableHead>
                <TableHead>Diagnóstico</TableHead>
                <TableHead>Tratamiento</TableHead>
                <TableHead>Veterinario</TableHead>
                <TableHead>Costo</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {consultations.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={6} className="text-center text-muted-foreground">
                    No hay consultas registradas para este paciente
                  </TableCell>
                </TableRow>
              ) : (
                consultations.map((consultation) => (
                  <TableRow key={consultation.id}>
                    <TableCell>{new Date(consultation.date).toLocaleDateString("es-MX")}</TableCell>
                    <TableCell>{consultation.reason}</TableCell>
                    <TableCell>{consultation.diagnosis}</TableCell>
                    <TableCell>{consultation.treatment}</TableCell>
                    <TableCell>{consultation.veterinarian}</TableCell>
                    <TableCell>${consultation.cost}</TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </div>
      </Card>
    </div>
  )
}
